import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
export default{
	data(){
		return{
			fileList:[],
			sex:'男',
			name:'',
			age:'',
			psd:'',
			phone:'',
			email:'',
		}
	},
	created(){
		//console.log("----created1----->",getIsShow())
	},
	methods:{
		//返回上一层
		backUpPage(){
			uni.switchTab({
			    url: '/pages/index/index'
			});
		},
		//注册
		register(){
			if(this.name && this.psd){
				uni.request({
					url: this.$baseURL+'/user_add',
					method:'POST',
					data:{
						name : this.name,
						psd : this.psd,
						age : this.age,
						sex : this.sex,
						img : this.fileList[0].url,
						phone : this.phone,
						email : this.email,
					},
					success: (res) => {
						console.log("-----注册------>",res);
						this.name=''
						this.psd=''
						this.age=''
						this.sex='男'
						this.phone=''
						this.email=''
						this.fileList=[]
						uni.showToast({
						    title: '注册成功',
						    duration: 500
						});
					}
				})
			}else{
				uni.showToast({
				    title: '请将信息输入完整',
				    duration: 500
				});
			}
		},
		getName(e){
			console.log("-----getName------>",e.detail.value);
			this.name=e.detail.value
		},
		getPsd(e){
			this.psd=e.detail
		},
		getAge(e){
			this.age=e.detail
		},
		getSex(e){
			this.sex=e.detail
		},
		getPhone(e){
			this.phone=e.detail
		},
		getEmail(e){
			this.email=e.detail
		},
		//上传图片
		afterRead(event){
			console.log("-----temp----->",event)
			const { file } = event.detail
			this.fileList=[]
			this.fileList.push({
				url:file.path
			})
		}
	}
}
